import { Link, useParams } from 'react-router-dom';
import axios from 'axios';
import { useEffect, useState } from 'react';
function Success() {
  const { orderId } = useParams();
  const [orderData, setOrderData] = useState({});
  const [formData, setFormData] = useState({});

  const getCart = async (orderId) => {
    const res = await axios.get(
      `/v2/api/${process.env.REACT_APP_API_PATH}/order/${orderId}`,
    );
    console.log(res);
    setOrderData(res.data.order);
  };
  const formCart = async (orderId) => {
    const res = await axios.get(
      `/v2/api/${process.env.REACT_APP_API_PATH}/order/${orderId}`,
    );
    console.log(res);
    setFormData(res.data.order.user);
  }; 
  const updateCart = async (orderId) => {
    const res = await axios.get(
      `/v2/api/${process.env.REACT_APP_API_PATH}/order/${orderId}`,
    );
    console.log(res);
    res.data.order.is_paid = true
    setOrderData(res.data.order);
  };
  useEffect(() => {
    getCart(orderId);
  }, [orderId]);
  useEffect(() => {
    formCart(orderId);
  }, [orderId]); 
 
  return (
    <div className='container full-height'>
       <div className="row row-cols-3 row-cols-sm-3 mt-4">
      <div className="col text-center">
        <div className="d-flex flex-column justify-content-center p-3 bg-secondary text-warning">
          <i className="bi bi-check2-circle display-5 my-2"></i>
          <span>STEP 1</span>
          <span className='mb-2'>確認訂單</span>
        </div>
      </div>
      <div className="col text-center">
      <div className="d-flex flex-column justify-content-center p-3 bg-secondary text-warning">
          <i className="bi bi-check2-circle display-5 my-2"></i>
          <span>STEP 2</span>
          <span className='mb-2'>建立訂單</span>
        </div>
      </div>
      {orderData.is_paid ? (
                <div className="col text-center">
                <div className="d-flex flex-column justify-content-center p-3 bg-secondary text-warning">
                  <i className="bi bi-check2-circle display-5 my-2"></i>
                  <span>STEP 3</span>
                  <span className='mb-2'>完成訂單</span>
                </div>
              </div>
      ):(
        <div className="col text-center">
                <div className="d-flex flex-column justify-content-center p-3" style={{backgroundColor:'#EBEDEE'}}>
                  <i className="bi bi-dash-circle-dotted display-5 my-2"></i>
                  <span>STEP 3</span>
                  <span className='mb-2'>完成訂單</span>
                </div>
              </div>
      ) }
    </div>
      <div className='mt-5 mb-7'>
        <div className='d-flex justify-content-center row'>
          {orderData.is_paid ? (
            <>
            <h2 className='fw-bolder text-center my-5'>訂單已完成付款，謝謝您的訂購！</h2>
             <div className='col-md-6'>
            <table class="table">
            <thead>
              <tr>
              <th><h4 className="fw-bolder p-2">訂單資訊<small className="text-danger fs-5 fw-normal">(已付款)</small></h4></th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td className='w-50 align-middle'>訂單金額</td>
                <td className='h2 text-danger'>{orderData.total}元</td>
              </tr>
              <tr>
                <td>訂單編號</td>
                <td>{orderData.id}</td>
              </tr>
              <tr>
                <td>email</td>
                <td>{formData.email}</td>
              </tr>
              <tr>
                <td>收件人姓名</td>
                <td>{formData.name}</td>
              </tr>
              <tr>
                <td>收件人電話</td>
                <td>{formData.tel}</td>
              </tr>
              <tr>
                <td>收件地址</td>
                <td>{formData.address}</td>
              </tr>
              <tr>
                <td colspan="2"><Link to={'/products'} className='w-100 btn btn-dark'>繼續選購</Link></td>
              </tr>
            </tbody>
          </table>
          </div>
            </>
          ) : (
          <>
            <div className='col-md-6'>
            <div className='card rounded-0 py-4'>
              <div className='card-header border-bottom-0 bg-white px-4 py-0'>
                <h2 className='fw-bolder'>訂購商品</h2>
              </div>
              <div className='card-body px-4 py-0'>
                <ul className='list-group list-group-flush'>
                {Object.values(orderData?.products || {}).map((item) => {
                    return (
                      <li className='list-group-item px-0' key={item.id}>
                        <div className='d-flex mt-2'>
                          <img
                            src={item.product.imageUrl}
                            alt=''
                            className='me-2'
                            style={{ width: '60px', height: '60px' }}
                          />
                          <div className='w-100 d-flex flex-column'>
                            <div className='d-flex justify-content-between fw-bold'>
                              <h5>{item.product.title}</h5>
                              <p className='mb-0'>x{item.qty}</p>
                            </div>
                            <div className='d-flex justify-content-between mt-auto'>
                              <p className='text-muted mb-0'>
                                <small>NT${item.product.price}</small>
                              </p>
                              <p className='mb-0'>NT${item.final_total}</p>
                            </div>
                          </div>
                        </div>
                        </li>
                    );
                  })}
                  <li className='list-group-item px-0 pb-0'>
                    <div className='d-flex justify-content-between mt-2'>
                    <p className='mb-0 h4 fw-bold'>總計</p>
                    <p className='mb-0 h4 fw-bold'>NT${orderData.total}</p>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
            </div>  
            <div className='col-md-6'>
            <table class="table">
            <thead>
              <tr colspan='2'>
              <th><h2 className="fw-bolder p-2">訂單資訊<small className="text-danger fs-5 fw-normal"> (未付款)</small></h2></th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td className='w-50 align-middle'>訂單金額</td>
                <td className='h2 text-danger'>{orderData.total}元</td>
              </tr>
              <tr>
                <td>訂單編號</td>
                <td>{orderData.id}</td>
              </tr>
              <tr>
                <td>email</td>
                <td>{formData.email}</td>
              </tr>
              <tr>
                <td>收件人姓名</td>
                <td>{formData.name}</td>
              </tr>
              <tr>
                <td>收件人電話</td>
                <td>{formData.tel}</td>
              </tr>
              <tr>
                <td>收件地址</td>
                <td>{formData.address}</td>
              </tr>
              <tr>
                <td colspan="2"><button className='w-100 btn btn-dark' onClick={() => {
                  updateCart(orderId);
                }}>信用卡付款</button></td>
              </tr>
            </tbody>
            </table>
            </div>
            </>  
          )
          } 
        </div>
      </div>
    </div>
  );
}

export default Success;