import axios from "axios";
import { useContext, useEffect, useState } from "react";
import {MessageContext,handleSuccessMessage,handleErrorMessage} from "../store/messageStore"


function ProductModal({ closeProductModal,getProducts,type,tempProduct }) {
  const [tempData, setTempData] = useState({
    title: '',
    category: '',
    origin_price: 100,
    price: 300,
    unit: '',
    power:'',
    diameter:'',
    water:'',
    function:'',
    description: '',
    content1: '',
    content2: '',
    is_enabled: 1,
    imageUrl: '',
    imageUrl1: '',
    imageUrl2: '',
  });

  const[,dispatch] = useContext(MessageContext);

  useEffect(() => {
    if(type === 'create'){
      setTempData( 
        {
          title: '',
          category: '',
          origin_price: 100,
          price: 300,
          unit: '',
          power:'',
          diameter:'',
          water:'',
          function:'',
          description: '',
          content1: '',
          content2: '',
          is_enabled: 1,
          imageUrl: '',
          imageUrl1: '',
          imageUrl2: '',
        })
    }else if (type === 'edit'){
      setTempData(tempProduct);
    }
  },[type,tempProduct])



  const handleChange = (e) => {
    const { value, name } = e.target;
    if (['price', 'origin_price'].includes(name)) {
      setTempData({
        ...tempData,
        [name]: Number(value),
      });
    } else if (name === 'is_enabled') {
      setTempData({
        ...tempData,
        [name]: +e.target.checked, // boolean
      });
    } else {
      setTempData({
        ...tempData,
        [name]: value,
      })
    }
  }

  const submit = async () => {
    try {
      let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product`;
      let method = 'post';
      if(type === 'edit') {
        api=`/v2/api/${process.env.REACT_APP_API_PATH}/admin/product/${tempProduct.id}`
        method = 'put'
      }
      const res = await axios[method](
        api, 
        {
          data: tempData
        }
      );
      console.log(res);
      handleSuccessMessage(dispatch, res);
      closeProductModal();
      getProducts();
    } catch (error) {
      console.log(error);
      handleErrorMessage(dispatch, error);
    }
  }

  return (
    <div
      className='modal fade'
      tabIndex='-1'
      id='productModal'
      aria-labelledby='exampleModalLabel'
      aria-hidden='true'
    >
      <div className='modal-dialog modal-lg'>
        <div className='modal-content'>
          <div className='modal-header'>
            <h1 className='modal-title fs-5' id='exampleModalLabel'>
              {type === 'create' ? '建立新商品' : `編輯 ${tempProduct.title}`}
            </h1>
            <button
              type='button'
              className='btn-close'
              aria-label='Close'
              onClick={closeProductModal}  />
         </div>
          <div className='modal-body'>
            <div className='row'>
              <div className='col-sm-4'>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='image'>
                    輸入圖片網址
                    <input
                      type='text'
                      name='imageUrl'
                      id='image'
                      placeholder='請輸入圖片連結'
                      className='form-control'
                      onChange={handleChange}
                      value={tempData.imageUrl}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                <label className='w-100' htmlFor='image1'>
                    輸入圖片1網址
                    <input
                      type='text'
                      name='imageUrl1'
                      id='image1'
                      placeholder='請輸入圖片1連結'
                      className='form-control'
                      onChange={handleChange}
                      value={tempData.imageUrl1}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                <label className='w-100' htmlFor='image2'>
                    輸入圖片2網址
                    <input
                      type='text'
                      name='imageUrl2'
                      id='image2'
                      placeholder='請輸入圖片2連結'
                      className='form-control'
                      onChange={handleChange}
                      value={tempData.imageUrl2}
                    />
                  </label>
                </div>
              </div>
              <div className='col-sm-8'>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='title'>
                    標題
                    <input
                      type='text'
                      id='title'
                      name='title'
                      placeholder='請輸入標題'
                      className='form-control'
                      onChange={handleChange}
                      value={tempData.title}
                    />
                  </label>
                </div>
                <div className='row'>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='category'>
                      分類
                      <input
                        type='text'
                        id='category'
                        name='category'
                        placeholder='請輸入分類'
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.category}
                      />
                    </label>
                  </div>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='unit'>
                      單位
                      <input
                        type='unit'
                        id='unit'
                        name='unit'
                        placeholder='請輸入單位'
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.unit}
                      />
                    </label>
                  </div>
                </div>
                <div className='row'>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='origin_price'>
                      原價
                      <input
                        type='number'
                        id='origin_price'
                        name='origin_price'
                        placeholder='請輸入原價'
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.origin_price}
                      />
                    </label>
                  </div>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='price'>
                      售價
                      <input
                        type='number'
                        id='price'
                        name='price'
                        placeholder='請輸入售價'
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.price}
                      />
                    </label>
                  </div>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='power'>
                      動力
                      <input
                        type='text'
                        id='power'
                        name='power'
                        placeholder='請輸入動力'
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.power}
                      />
                    </label>
                  </div>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='diameter'>
                      錶面直徑
                      <input
                        type='text'
                        id='diameter'
                        name='diameter'
                        placeholder='請輸入表面直徑'
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.diameter}
                      />
                    </label>
                  </div>
                </div>
                <div className='row'>
                <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='water'>
                      防水
                      <input
                        type='text'
                        id='water'
                        name='water'
                        placeholder='請輸入防水'
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.water}
                      />
                    </label>
                  </div>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='function'>
                      功能
                      <input
                        type='text'
                        id='function'
                        name='function'
                        placeholder='請輸入功能'
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.function}
                      />
                    </label>
                  </div>
                </div>
                <hr />
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='description'>
                    產品描述
                    <textarea
                      type='text'
                      id='description'
                      name='description'
                      placeholder='請輸入產品描述'
                      className='form-control'
                      onChange={handleChange}
                      value={tempData.description}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='content1'>
                    說明內容1
                    <textarea
                      type='text'
                      id='content1'
                      name='content1'
                      placeholder='請輸入產品說明內容1'
                      className='form-control'
                      onChange={handleChange}
                      value={tempData.content1}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='content2'>
                    說明內容2
                    <textarea
                      type='text'
                      id='content2'
                      name='content2'
                      placeholder='請輸入產品說明內容2'
                      className='form-control'
                      onChange={handleChange}
                      value={tempData.content2}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <div className='form-check'>
                    <label
                      className='w-100 form-check-label'
                      htmlFor='is_enabled'
                    >
                      是否啟用
                      <input
                        type='checkbox'
                        id='is_enabled'
                        name='is_enabled'
                        className='form-check-input'
                        onChange={handleChange}
                        checked={!!tempData.is_enabled}
                      />
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className='modal-footer'>
            <button
              type='button'
              className='btn btn-secondary'
              onClick={closeProductModal}>
                關閉
              </button>
              <button type='button' className='btn btn-primary' onClick={submit}>
              儲存
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
export default ProductModal;
