import axios from 'axios';
import { useState } from 'react';
import { Link, useOutletContext } from 'react-router-dom';

function Cart() {
  const { cartData, getCart } = useOutletContext();
  const [loadingItems, setLoadingItem] = useState([]);

  const removeCartItem = async (id) => {
    try {
      const res = await axios.delete(
        `/v2/api/${process.env.REACT_APP_API_PATH}/cart/${id}`,
      );
      console.log(res);
      getCart();
    } catch (error) {
      console.log(error);
    }
  };

  const updateCartItem = async (item, quantity) => {
    const data = {
      data: {
        product_id: item.product_id,
        qty: quantity,
      },
    };
    setLoadingItem([...loadingItems, item.id]);
    try {
      const res = await axios.put(
        `/v2/api/${process.env.REACT_APP_API_PATH}/cart/${item.id}`,
        data,
      );
      console.log(res);
      setLoadingItem(
        loadingItems.filter((loadingObject) => loadingObject !== item.id),
      );
      getCart();
    } catch (error) {
      console.log(error);
    }
  };
  return (<>
           <div className="d-flex justify-content-center align-items-center"
            style={{
                minHeight: '300px',
                backgroundImage:
                    'url(https://images.unsplash.com/photo-1528698827591-e19ccd7bc23d?q=80&w=2076&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D)',
                backgroundPosition: 'center center',
                }}>
                <p className="text-light display-2 fw-bold">Cart</p>
            </div>
    <div className='container'>
      <div className='row justify-content-center'>
          {cartData?.carts?.length === 0 ? (
            <>
             <div className='col-md-6 bg-white py-5 full-height'>
              <div className='alert alert-secondary fw-bold '>還沒有選擇手錶嗎?</div>
              <Link
                to='/products'
                className='btn btn-dark w-100 mt-4 rounded-0 py-3'
              >
                選擇專屬於您的手錶
              </Link>
              </div>
            </>
          ) : (
              <div>
                  {/* 表格 */}
                  <table className="table my-3">
                     <thead>
                       <tr>
                         <th scope="col" style={{ width: '500px' }}>產品名稱</th>
                         <th scope="col" style={{ width: '300px' }} className='text-center'>數量</th>
                         <th scope="col" style={{ width: '100px' }} className='text-nowrap text-end'>刪除</th>
                       </tr>
                    </thead>
              {cartData?.carts?.map((item) => {
                return (            
                <>
                     <tbody key={item.id}>
                       <tr>
                         <td className='d-flex-colunm d-sm-flex align-items-center' >
                          <img src={item.product.imageUrl} alt='' className='object-cover'  style={{ width: '180px' }}/>              
                          <p className='fw-bold mt-2 ms-2'>{item.product.title}</p>
                          </td>
                         <td>
                          <p className='text-center mt-5 fw-bold fs-5'>合計{item.final_total}元</p>
                          <div className='input-group w-100'>
                          <select
                            name=''
                            className='form-select'
                            id=''
                            value={item.qty}
                            disabled={loadingItems.includes(item.id)}
                            onChange={(e) => {
                              updateCartItem(item, e.target.value * 1);
                            }}
                          >
                            {[...new Array(20)].map((i, num) => {
                              return (
                                <option value={num + 1} key={num}>
                                  {num + 1}
                                </option>
                              );
                            })}
                          </select>
                        </div>
                          </td>
                         <td className='position-relative'>
                          <button
                        type='button'
                        className='btn mt-6 position-absolute end-0'
                        onClick={() => {
                          removeCartItem(item.id);
                        }}
                      >
                       <i className="bi bi-trash-fill text-danger fs-4"></i>
                      </button></td>
                      </tr>
                       
                     </tbody> 
                     </>
                  )  
                  })}
                  </table>
              <div className='d-flex justify-content-end mt-4'>
                <h3 className='text-danger mb-0 fw-bold'>總價NT${cartData.final_total}</h3>
              </div>
              <div className="d-flex justify-content-between mb-5">
                <Link to='/products' className='btn btn-outline-dark mt-4 rounded-0 p-3 fw-bolder'>繼續選購</Link>
                <Link
                to='/checkout'
                className='btn btn-dark mt-4 rounded-0 p-3 fw-bolder'
              >
                下一步
              </Link> 
              </div>
             
            </div>
          )}
          </div>
        </div>
        </>
  );
}

export default Cart;