import { useEffect, useState } from 'react';
import axios from 'axios';
import { useOutletContext, useParams } from 'react-router-dom';

function ProductDetail() {
  const [product, setProduct] = useState({});
  const [cartQuantity,setCartQuantity] = useState(1);
  const { id } = useParams();
  const [isLoading,setIsLoading] = useState(false);
  const {getCart} = useOutletContext();

  const getProduct = async (id) => {
    const productRes = await axios.get(
      `/v2/api/${process.env.REACT_APP_API_PATH}/product/${id}`,
    );
    setProduct(productRes.data.product);
  };
  const addToCart = async () => {
    const data = {
      data:{
        product_id: product.id,
        qty:cartQuantity,
      },
    }
    setIsLoading(true);
    try {
       const res = await axios.post( `/v2/api/${process.env.REACT_APP_API_PATH}/cart`,data)
       console.log(res);
       getCart();
       setIsLoading(false);
    } catch (error) {
      console.log(error);
      setIsLoading(false);
    }
  }
  useEffect(() => {
    getProduct(id);
  }, [id]);
  return (
    <div className='container full-height'>
      <div className='row my-5'>
      <div className='col-md-6 my-5'
        style={{
          minHeight: '500px',
          backgroundImage:
            `url(${product.imageUrl})`,
          backgroundPosition: 'center center',
        }}
      ></div>
      
        <div className='col-md-6 mt-5 position-relative'>
          <h2 className='pb-2 mb-5 border-bottom border-secondary border-1'>{product.title}</h2>
           <p>
            {product.description}
          </p>
          <p className='my-2 text-danger fw-bold h5'><s>NT$ {product.origin_price}</s></p>
          <p className='fw-bold h4 my-3'>NT$ {product.price}</p>
         
        
          <div className='input-group mt-5 mb-3 border top-0'>
            <div className='input-group-prepend'>
              <button
                className='btn btn-outline-dark rounded-0 border-0 py-2'
                type='button'
                id='button-addon1'
                onClick={() =>
                  setCartQuantity((pre)=> pre === 1 ? pre : pre-1)
              }
              >
                <i className='bi bi-dash'></i>
              </button>
            </div>
            <input
              type='number'
              className='form-control border-0 text-center my-auto shadow-none'
              placeholder=''
              aria-label='Example text with button addon'
              aria-describedby='button-addon1'
              readOnly
              value={cartQuantity}
            />
            <div className='input-group-append'>
              <button
                className='btn btn-outline-dark rounded-0 border-0 py-2 '
                type='button'
                id='button-addon2'
                onClick={() =>
                  setCartQuantity((pre)=> pre+1)
              }
              >
                <i className='bi bi-plus'></i>
              </button>
            </div>
          </div>
          <button
          type='button'
            href='./checkout.html'
            className='btn btn-dark w-100 rounded-0 py-3' 
            onClick={() => {
              addToCart()
            }}
            disabled={isLoading}
          >
            加入購物車
          </button>
        </div> 
        </div> 
        <div className="row flex-row-reverse align-items-center">  
      <div className="col-md-6 mb-5">
        <img src={product.imageUrl1} className='img-fluid' alt="" />
      </div>  
      <div className="col-md-6 mb-5 px-5">
        {product.content1}
      </div>  
      </div>
      <div className="row align-items-center border-bottom border-dark border-1">
      <div className="col-md-6 mb-5">
        <img src={product.imageUrl2} className='img-fluid' alt="" />
      </div>
      <div className="col-md-6 mb-5 px-5">
        {product.content2}
      </div>
      </div>
      <div className="row mt-2 gy-5 justift-content-center mx-4 mb-4">
            <div className="col-sm-4 mb-3">
              <h3 className='fw-bolder'>產品規格</h3>
            </div>
            <div className="col-sm-8 mb-3">
                <div className="row">
                  <div className="col-md-4 mb-3 px-3">
                  <span className='fw-bolder'><i className="bi bi-gear-fill"></i> 動力 : </span>{product.power}
                  </div>
                  <div className="col-md-4 mb-3 px-3">
                  <span className='fw-bolder'><i className="bi bi-watch"></i> 錶面直徑 : </span>{product.diameter}
                  </div>
                  <div className="col-md-4 mb-3 px-3">
                  <span className='fw-bolder'><i className="bi bi-droplet-fill"></i> 防水 : </span>{product.water}
                  </div>
                  <div className="col-md-4 px-3">
                  <span className='fw-bolder'><i className="bi bi-motherboard-fill"></i> 功能 : </span>{product.function}
                  </div>
                </div>
            </div>
            <div className="col-sm-4 mb-3">
              <h3 className='fw-bolder'>購物須知</h3>
            </div>
            <div className="col-sm-8 mb-3 px-3">
            您下單後我們會立即確認訂單，並且安排出貨，預計 3~5 天內收到您訂購的商品。 凡是購買 WATCH 全系列錶款，在 3 年保固期間都可以拿到我們服務據點免費維修，超過保固期間也能享有維修折扣。
            </div>
            <div className="col-sm-4 mb-3">
              <h3 className='fw-bolder'>退換貨說明</h3>
            </div>
            <div className="col-sm-8 mb-3 px-3">
            在 WATCH 官網購物，享有商品到貨日起 10 天猶豫期，猶豫期內，可無條件退費。猶豫期並非試用期， 所以，您所退回的商品必須是包裝完整、保存良好的狀態。
            </div>
      </div>
    </div>
  );
}

export default ProductDetail;