import { useEffect, useState } from "react";
import axios from 'axios'
import Pagination from "../../components/Pagination";
import { Link,useOutletContext } from "react-router-dom";
import Loading from "../../components/Loading";

function Products() {
  const [products, setProducts] = useState([]);
  const [pagination, setPagination] = useState({});
  const [isLoading,setIsLoading] = useState(false);
  const {getCart} = useOutletContext();
  const getProducts = async (page = 1) => {
    setIsLoading(true);
    const productRes = await axios.get(
      `/v2/api/${process.env.REACT_APP_API_PATH}/products/?page=${page}`,
    );
    console.log(productRes);
    setProducts(productRes.data.products);
    setPagination(productRes.data.pagination);
    setIsLoading(false);
  }
    useEffect(()=>{
      getProducts();
    },[])

    return (<>
    {/* 標題圖 */}
     <div className="d-flex justify-content-center align-items-center"
     style={{
          minHeight: '300px',
          backgroundImage:
            'url(https://images.unsplash.com/photo-1488132828189-4e416661b680?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D)',
          backgroundPosition: 'center center',
        }}>
         <p className="text-light display-2 fw-bold">Product</p>
      </div>
      {/* 內容 */}
      <div className="container mt-md-5 mt-3 mb-7 full-height">
        <Loading isLoading={isLoading}/>
        <div className="row row-cols-1 row-cols-md-2 row-cols-lg-4 g-2 mb-4">
          {products.map((product)=>{
            return(
              <div className="card" key={product.id}>
              <Link to={`/product/${product.id}`} className="text-decoration-none card-body text-center">
                <img src={product.imageUrl} className="card-img-top rounded-0 object-cover p-4" height={300} alt="..." />
              <h5 className="card-title fw-bolder mb-3">{product.title}</h5>
              <div className="card-text my-2">
               <span className="fs-5 me-3">NT$ {product.price}</span>
               <del className="text-muted">NT$ {product.origin_price}</del>
              </div>
              </Link>
              <div className="d-flex card-footer justify-content-between">
                <Link to={`/product/${product.id}`} className="btn btn-light">查看更多</Link>
                <button type="button" className="btn btn-dark"
                onClick={
                  async () => {
                    const data = {
                      data:{
                        product_id: product.id,
                        qty:1,
                      },
                    }
                    setIsLoading(true);
                    try {
                       const res = await axios.post( `/v2/api/${process.env.REACT_APP_API_PATH}/cart`,data)
                       console.log(res);
                       getCart();
                       setIsLoading(false);
                    } catch (error) {
                      console.log(error);
                      setIsLoading(false);
                    }
                  }
                }>加入購物車</button>
              </div>
          </div>
            )
          })}
        </div>
        <nav className="d-flex justify-content-center">
         <Pagination pagination={pagination} changePage={getProducts}/>
        </nav>
      </div>
    </>)
  }
  
  export default Products;